/* Projects */

.projects .technology_header h5 {
    font-weight: 900;
    color: #555;
}

.projects .project-card {
    float: left;
    width: 100%;
    min-height: 250px;
    padding: 15px;
    background: none;
}

.projects .project-card .card-body {
    border-radius: 4px;
    background: rgba(85, 85, 85, 0.226);
}

.projects .project-card .card-body h3 {
    color: #555;
    text-transform: capitalize;
    font-family: 'Barlow', sans-serif;
    margin-bottom: 10px;
}

.projects .project-card .card-body p {
    font-size: 15px;
    line-height: 1.8rem;
    color: rgb(141, 138, 138);
    font-family: 'Roboto', sans-serif;
    margin-bottom: 10px;
}

.projects .project-card .card-body .btn {
    font-style: 30px;
    border: 1px solid #313131;
    padding: 5px 10px;
}

.projects .show-more-btn {
    color: #b11654;
    font-size: 17px;
    font-family: 'Roboto', sans-serif;
    border: 1px solid #b11654;
    border-radius: 0;
    padding: 10px 40px;
    transition: 0.4s all;
}

.projects .show-more-btn:hover {
    color: #fff;
    background: #b11654;
}

@media(min-width: 576px) {
    .projects .project-card {
        width: 50%;
    }
}

@media(min-width: 1250px) {
    .projects .project-card {
        width: 33.33%;
    }
}

